<template>
    <div>
        <div class="register register-l">
            <el-form
            :model="LoginForm"
            status-icon
            :rules="rules2"
            ref="LoginForm"
            class="demo-ruleForm"
            > 
            <el-form-item  label=":" prop="userCode" label-width="120px">
                <el-input v-model="LoginForm.userCode" auto-complete="off" placeholder=""></el-input>
            </el-form-item>

            <el-form-item prop="userPwd" label="" label-width="120px">
                <el-input type="password" v-model="LoginForm.userPwd" auto-complete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="checkPass" label="" label-width="120px">
                <el-input type="password" v-model="LoginForm.checkPass" auto-complete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click = "submitModify('LoginForm')" style="width:20%;margin-left: 12vw;"></el-button>
                <el-button type="primary" @click = "gotoLogin" style="width:20%;"></el-button>
            </el-form-item>
            </el-form>
        </div>
  </div>
</template>
<script> 

export default {
  name: "Register", 
  data() {
    // <!---->
    let validatePass = (rule, value, callback) => {
        if (value === "") {
            callback(new Error(""))
        } else {
            if (this.LoginForm.checkPass !== "") {
            this.$refs.LoginForm.validateField("checkPass");
            }
            callback()
        }
    }
    // <!---->
    let validatePass2 = (rule, value, callback) => {
        if (value === "") {
            callback(new Error(""));
        } else if (value !== this.LoginForm.userPwd) {
            callback(new Error("!"));
        } else {
            callback(); 
        }
    };
    return { 
        LoginForm: {
            userPwd: "",
            userCode:"",
            checkPass:"",
        },
        rules2: {
            userCode: [{ required: true, message: '' }],
            userPwd: [{ required: true,validator: validatePass, trigger: 'change' }],
            checkPass: [{ required: true,validator: validatePass2, trigger: 'change' }],
        },
    }
  }, 
  methods: {
     submitModify(formName) {
        let self = this;
        this.$refs[formName].validate(valid => {
        if (valid) {
            this.$store.dispatch('submitModify',{"userCode":self.LoginForm.userCode, "userPwd":self.LoginForm.userPwd,
                }).then((rep) => {
                if(rep.data.code == 0){
                    alert(','+rep.data.data.userPwd)
                    this.LoginForm = {
                        userPwd: "",
                        checkPass:"",
                    };
                }else {
                    alert(rep.data.message)
                    return false;
                }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      })
    },
    // <!---->
    gotoLogin() {
      this.$router.push({
        path: "/"
      });
    },

  }
};
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.register-l{
    float: left;
    width: 38%;
    margin: 10px 10px 0 18vw;
}
.btn-search {
    width: 4vw;
    // line-height: 5vh;
    line-height: 25px;
    color: #fff;
    background-color: #4f71cf;    
}

.el-input{
    width: 80%;
}
</style>


