                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="partnerWebddr" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerWebddr" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="partnerTech" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerTech"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="partnerDate" label-width="130px" >
                                <el-date-picker
                                type="date"
                                v-model="FormModel.partnerDate"
                                style="width: 100%;"
                                :editable="true"
                                :picker-options="birthOptions"
                                @change="getSTime">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="partnerChannel" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerChannel" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="partnerTurnover" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerTurnover" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="partnerRegistcapital" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerRegistcapital" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- end -->          
                <!-- start -->
                <el-collapse-item title="" name="2">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="name" label-width="130px" >
                                <el-input v-model.trim="FormModel.name" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" label-width="130px" >
                                <el-input v-model.trim="FormModel.title" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="gender" label-width="130px">
                                <el-select  v-model="FormModel.gender"  placeholder="" class="gender-select">
                                    <el-option
                                        v-for="item in gender"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="phoneNumber" label-width="130px" >
                                <el-input v-model.trim="FormModel.phoneNumber" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="emailAddress" label-width="130px" >
                                <el-input v-model.trim="FormModel.emailAddress" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="epartnerpartnerName" label-width="130px" >
                                <el-input v-model.trim="FormModel.epartnerpartnerName" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- end -->
                <!-- start -->
                <el-collapse-item title="" name="3">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="epartnerpartnerName" label-width="130px" >
                                <el-input v-model.trim="FormModel.epartnerpartnerName" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="Callin" prop="partnerCallintime" label-width="130px" >
                                <el-date-picker
                                type="date"
                                v-model="FormModel.partnerCallintime"
                                style="width: 100%;"
                                :editable="true"
                                :picker-options="birthOptions"
                                @change="getSTime">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="Callin BD Owner" prop="partnerBdOwner" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerBdOwner" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="partnerCostage" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerCostage" :disabled="ifMotify" placeholder="Callin"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="signContract" label-width="130px">
                                <el-select  v-model="FormModel.signContract"  placeholder="" class="gender-select">
                                    <el-option
                                        v-for="item in signContract"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="contractDate" label-width="130px" >
                                <el-date-picker
                                    type="date"
                                    v-model="FormModel.contractDate"
                                    style="width: 100%;"
                                    :editable="true"
                                    :picker-options="birthOptions"
                                    @change="getSTime">
                                    </el-date-picker>
                                </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="entrust" label-width="130px">
                                <el-select  v-model="FormModel.entrust"  placeholder="" class="gender-select">
                                    <el-option
                                        v-for="item in entrust"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="entrustName" label-width="130px" >
                                <el-input v-model.trim="FormModel.entrustName" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="partnerAwarding" label-width="130px">
                                <el-select  v-model="FormModel.partnerAwarding"  placeholder="" class="gender-select">
                                    <el-option
                                        v-for="item in partnerAwarding"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label=" " prop="projectName" label-width="130px" >
                                <el-input v-model.trim="FormModel.projectName" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="coType" label-width="130px" >
                                <el-input v-model.trim="FormModel.coType" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="coProgress" label-width="130px" >
                                <el-input v-model.trim="FormModel.coProgress" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="Fii" prop="fiiCodepartment" label-width="130px" >
                                <el-input v-model.trim="FormModel.fiiCodepartment" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <!--    :action="`${$$.BASE_URL}${uploadApi}`"
                             :action="'http://127.0.0.1:8080/tsbg/company/addCompany
                             http://127.0.0.1:80/tsbg/upload/import'"
                                    -->
                            <el-form-item label="" label-width="130px">
                                <el-upload
                                ref="upload"
                                :action="`${BASE_URL}/tsbg/upload/importmore`"
                                :headers="headers"
                                :file-list="fileList"
                                :data="otherData"
                                :on-remove="fileRemvoeFunc"
                                :on-error="fileErrorFunc"
                                :before-upload="beforeUploadFunc"
                                :on-change="handleUploadChange"
                                :on-success="uploadCtrlFunc"
                                :on-exceed="handleExceed"
                                :limit="15" multiple
                                accept=".pdf, .doc, .docx, .xlsx, .xls, .xlsm, .ppt, .pptx"
                                
                                :auto-upload="false"
                                :http-request="uploadSectionFile">
                                <el-button slot="trigger" type="plain" style="height:33px;font-size:1em;">
                                    
                                </el-button>
                                <div slot="tip" class="el-upload__tip">pdfwordexcelppttxt30M</div>
                                <!-- <p>{{ fileLimitSize }}M</p> -->
                                <!-- <span
                                    slot="tip"
                                    v-if="tipFlag"
                                    class="el-upload__tip font-size-small ml10">
                                    
                                </span> -->
                                </el-upload>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                    <!-- end -->
                    <!--  -->
                    <el-form-item style="text-align:center;margin-top:25px;">
                        <!-- <el-button type="primary" @click = "saveCompany('FormModel')"></el-button> -->
                        <el-button type="primary" @click = "addCompany('FormModel')" :disabled="isDisable"></el-button>
                        <el-button type="primary" @click = "gotoLogin"></el-button>
                    </el-form-item>
                </el-form> 
                </el-collapse>  
            </div>
        </div>
    </div> 
    
</template>
<script>
import { BASE_URL } from '@/store/api'
export default {
    data(){
    let checkTel = (rule, value, callback) => {
        if (value === '') {
            callback(new Error(''))
        }else if (!this.checkMobile(value)) {
            callback(new Error(''))
        }else {
            callback()
        }
    }
    return {
        firName:localStorage.getItem('FIRNAME'),
        secName:localStorage.getItem('SECNAME'),
        // 
        FormModel: {
            partnerName: '',
            partnerIndustry: '',
            partnerRegion: '',
            partnerProduct: '',
            name: '',
            gender: '',
            phoneNumber: '',
        },
        activeNames: ['1','2','3'],
        gender: [
            {label: '', value: 1 },
            {label: '', value: 0 }
        ],
        partnerList: [                //
            {label: '', value: 1 },
            {label: '', value: 0 }
        ],
        signContract: [                //
            {label: '', value: 1 },
            {label: '', value: 0 }
        ],
        entrust: [                     //
            {label: '', value: 1 },
            {label: '', value: 0 }
        ], 
        partnerAwarding: [             //
            {label: '', value: 1 },
            {label: '', value: 0 }
        ],
        // 
        rules: {
            epartnerpartnerName: [{ required: true, message: '' }],
            // ecooperationpartnerName: [{ required: true, message: ''}],
            // epartnerPartnerName: [{ required: true, message: '' }],
            partnerIndustry: [{ required: true, message: '' }],
            partnerRegion: [{ required: true, message: ''}],
            partnerProduct: [{ required: true, message: '//'}],
            name: [{ required: true, message: ''}],
            gender: [{ required: true, message: ''}],
            phoneNumber: [{required: true,  trigger: 'change'},{validator: checkTel}],
        },
        // 
        fileList: [],
        // 
        uploadApi: '',
        // 
        otherData: null,
        postparam : {
            ecooperation:{  },
            epartner : {},
            eccontacts: {},
            userInfo:{},
            file:null,
        },
        isDisable: false, 
        }
    },
    components: {
    
    },
    mounted () {
     
    },
    methods: {
    // 
    fileRemvoeFunc (file, fl) {
      // this.fileList = fl
      fl.length === 0 && (this.tipFlag = true)
    },
    // 
    uploadCtrlFunc (res, file, fl) {
        this.fileList = fl
        this.loading = false
        this.$emit('confirm')
        // this.done()
        this.$message({
            type: 'success',
            message: ''
        })
        this.$refs.upload.clearFiles()
    },
    // 
    fileErrorFunc (err, file, fileList) {
        this.loading = false
        console.log('error: ', err, file, fileList)
        this.$message({
            type: 'error',
            message: ''
        })
    },
    // loading
    beforeUploadFunc (file) {
        if (file) {
            this.loading = true
            let fileLimitSize = 30
            if (file.size) {
                const limitSize = file.size / 1024 / 1024 < fileLimitSize
                // const limitSize
                if (!limitSize)  {
                    // debugger
                  this.$message.warning(` ${fileLimitSize} MB!`)
                }
                return  limitSize && fileLimitSize
            }
        }
    },

    // 
    handleExceed (file, fileList) {
        this.$message({
            message: '',
            type: 'warning'
        })
    },
    // 
    handleUploadChange (file, fl) {
        fl.length > 0 && (this.tipFlag = false)
      // this.fileList = JSON.parse(JSON.stringify(fl))
    },
    // 
    uploadSectionFile(file){
        this.postparams.append('file', file.file);
    },
    addCompany(FormModel) {
        this.isDisable = true
        let postparam = {
            // let params = {
            ecooperation : {
                partnerName:this.FormModel.epartnerpartnerName,
                partnerCallintime:this.FormModel.partnerCallintime,
                partnerBdOwner:this.FormModel.partnerBdOwner,
                partnerCostage:this.FormModel.partnerCostage,
                signContract:this.FormModel.signContract,
                contractDate:this.FormModel.contractDate,
                entrust:this.FormModel.entrust,
                entrustName:this.FormModel.entrustName,
                partnerAwarding:this.FormModel.partnerAwarding,
                projectName:this.FormModel.projectName,
                coType:this.FormModel.coType,
                coProgress:this.FormModel.coProgress,
                fiiCodepartment:this.FormModel.fiiCodepartment,
                },
            epartner : {
                partnerName:this.FormModel.epartnerpartnerName,
                partnerIndustry:this.FormModel.partnerIndustry,
                partnerRegion:this.FormModel.partnerRegion,
                partnerProduct:this.FormModel.partnerProduct,
                partnerList:this.FormModel.partnerList,
                partnerScale:this.FormModel.partnerScale,
                partnerAddr:this.FormModel.partnerAddr,
                partnerWebddr:this.FormModel.partnerWebddr,
                partnerTech:this.FormModel.partnerTech,
                partnerDate:this.FormModel.partnerDate,
                partnerChannel:this.FormModel.partnerChannel,
                partnerTurnover:this.FormModel.partnerTurnover,
                partnerRegistcapital:this.FormModel.partnerRegistcapital,
            },
            eccontacts : {
                name:this.FormModel.name,
                gender:this.FormModel.gender,
                phoneNumber:this.FormModel.phoneNumber,
                partnerName:this.FormModel.epartnerpartnerName,
                emailAddress:this.FormModel.emailAddress,
                title:this.FormModel.title,
            },
            userInfo : {
                userName:localStorage.getItem('USERNAME'),
                userCode:localStorage.getItem('USERCODE')
            },
        }  
        // 
        this.postparams = new FormData()
        this.postparams.append('ecooperation',JSON.stringify(postparam.ecooperation));
        this.postparams.append('epartner', JSON.stringify(postparam.epartner));
      
        this.postparams.append('eccontacts', JSON.stringify(postparam.eccontacts));
        this.postparams.append('userInfo', JSON.stringify(postparam.userInfo));

        this.$refs[FormModel].validate(valid => {
        if (valid) {
            this.$refs.upload.submit()
            this.$store.dispatch('addCompany',this.postparams).then((rep) => {
                if(rep.data.code == 0){
                    alert('')
                    setTimeout(() => {
                        this.isDisable = false
                    }, 1000)
                    this.FormModel = {};
                    this.$refs.upload.clearFiles()//
                }else {
                    alert(rep.data.message)
                    this.isDisable = false
                    return false;
                }
            });
            
        } else {
            console.log("error submit!!");
            alert("")
            this.isDisable = false
            return false;
        }
      })
    
    },

    // <!---->
    gotoLogin() {
        this.$router.go(-1);
    },
    // 
    checkMobile(str) {
        let re = /^1\d{10}$/
        if (re.test(str)) {
            return true;
        }else {
            return false;
        }
    },

    }
}
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.register-l{
    float: left;
    width: 95%;
    margin: 30px;
}
.el-input{
    width: 100%;
}
.el-select{
    width: 100%;
}

</style>


