<script>
import { debug } from 'util';
import { BASE_URL } from '@/store/api'
export default {
    name: "",
    data(){
        let checkTel = (rule, value, callback) => {
        if(value === '') {
            callback(new Error(''))
        }else if (!this.checkMobile(value)) {
            callback(new Error(''))
        }else {
            callback()
        }
    }
    return {
        partnerNo:localStorage.getItem('PARTNERNO'),
        activeNames: ['1'],
        // 
        FormModel: {},
        gender: [
            {label: '', value: 1 },
            {label: '', value: 0 },
            {label: '', value: 2 }
        ],
        partnerList: [                //
            {label: '', value: 1 },
            {label: '', value: 0 }
        ],
        signContract: [                //
            {label: '', value: 1 },
            {label: '', value: 0 }
        ],
        entrust: [                     //
            {label: '', value: 1 },
            {label: '', value: 0 }
        ], 
        partnerAwarding: [             //
            {label: '', value: 1 },
            {label: '', value: 0 }
        ], 
        rules: {
            epartnerpartnerName : [{ required: true, message: ''}],
            // ecooperationpartnerName: [{ required: true, message: ''}],
            epartnerpartnerIndustry: [{ required: true, message: '' }],
            epartnerpartnerRegion: [{ required: true, message: ''}],
            epartnerpartnerProduct: [{ required: true, message: '//' }],
            // eccontactspartnerName: [{ required: true, message: ''}],
            eccontactsname: [{ required: true, message: ''}],
            eccontactsgender: [{ required: true, message: '' }],
            eccontactsphoneNumber: [{ required: true, message: ''},{validator: checkTel}],
                
        }, 
        fileLists:[],
        newfileList: [],
        fileListshow: false ,
        isDisable: false,    
    }
    },
    components: {
    //   Home
    },
    mounted () {
        this.open()
        this.filedetail()
    },
    methods: {
    //
    open(){
        this.$store.dispatch('getEcooperationList', {partnerNo:this.partnerNo})
        .then(res => {
            if(res.data.code == 0){
                let dataContet = res.data.data[0]
                    this.FormModel = {
                        ecooperationpartnerName: dataContet.ecooperation ? dataContet.ecooperation[0].partnerName:'',
                        ecooperationpartnerCallintime: dataContet.ecooperation ? dataContet.ecooperation[0].partnerCallintime:'',
                        ecooperationpartnerBdOwner: dataContet.ecooperation ?dataContet.ecooperation[0].partnerBdOwner:'',
                        ecooperationpartnerCostage: dataContet.ecooperation ?dataContet.ecooperation[0].partnerCostage:'',
                        ecooperationsignContract: dataContet.ecooperation ? dataContet.ecooperation[0].signContract:'',
                        ecooperationcontractDate: dataContet.ecooperation ? dataContet.ecooperation[0].contractDate:'',
                        ecooperationentrust: dataContet.ecooperation ? dataContet.ecooperation[0].entrust:'',
                        ecooperationentrustName: dataContet.ecooperation ? dataContet.ecooperation[0].entrustName:'',
                        ecooperationpartnerAwarding: dataContet.ecooperation ? dataContet.ecooperation[0].partnerAwarding:'',
                        ecooperationprojectName: dataContet.ecooperation ? dataContet.ecooperation[0].projectName:'',
                        ecooperationcoType: dataContet.ecooperation ? dataContet.ecooperation[0].coType:'',
                        ecooperationcoProgress: dataContet.ecooperation ? dataContet.ecooperation[0].coProgress:'',
                        ecooperationfiiCodepartment: dataContet.ecooperation ? dataContet.ecooperation[0].fiiCodepartment:'',
                
                        epartnerpartnerName: dataContet.epartner ? dataContet.epartner.partnerName:'',
                        epartnerpartnerIndustry: dataContet.epartner ? dataContet.epartner.partnerIndustry:'',
                        epartnerpartnerRegion: dataContet.epartner ? dataContet.epartner.partnerRegion:'',
                        epartnerpartnerProduct: dataContet.epartner ? dataContet.epartner.partnerProduct:'',
                        epartnerpartnerList: dataContet.epartner ? dataContet.epartner.partnerList:'',
                        epartnerpartnerScale: dataContet.epartner ? dataContet.epartner.partnerScale:'',
                        epartnerpartnerAddr: dataContet.epartner ? dataContet.epartner.partnerAddr:'',
                        epartnerpartnerWebddr: dataContet.epartner ? dataContet.epartner.partnerWebddr:'',
                        epartnerpartnerTech: dataContet.epartner ? dataContet.epartner.partnerTech:'',
                        epartnerpartnerDate: dataContet.epartner ? dataContet.epartner.partnerDate:'',
                        epartnerpartnerChannel: dataContet.epartner ? dataContet.epartner.partnerChannel:'',
                        epartnerpartnerTurnover: dataContet.epartner ? dataContet.epartner.partnerTurnover:'',
                        epartnerpartnerRegistcapital: dataContet.epartner ? dataContet.epartner.partnerRegistcapital:'',
                
                        eccontactsname: dataContet.eccontacts ? dataContet.eccontacts[0].name:'',
                        eccontactsgender: dataContet.eccontacts ? dataContet.eccontacts[0].gender:'',
                        eccontactsphoneNumber: dataContet.eccontacts ? dataContet.eccontacts[0].phoneNumber:'',
                        eccontactspartnerName: dataContet.eccontacts ? dataContet.eccontacts[0].partnerName:'',
                        eccontactsemailAddress: dataContet.eccontacts ? dataContet.eccontacts[0].emailAddress:'',
                        eccontactstitle: dataContet.eccontacts ? dataContet.eccontacts[0].title:'',
                    
                    } 
            }
        })
    },
    filedetail(){
        this.$store.dispatch('filedetail', {partnerNo:this.partnerNo})
        .then(res => {
            if(res.code == 0){
                this.fileLists = res.dataSecond
                this.fileAll = res.dataSecond//
                if(this.fileLists.length <= 0){
                    this.fileListshow = true
                }else if(this.fileLists.length < 15){
                    this.fileListshow = false
                }
                
            }
        })
    },
    getDownload (file) {
    //   return `http://127.0.0.1:80/tsbg/upload/testdownload?fileName=`+file+`&partnerNo=`+this.partnerNo
      return `${BASE_URL}/tsbg/upload/testdownload?fileName=`+file+`&partnerNo=`+this.partnerNo
    },
    download (file) {
        const form = document.createElement('form')
        const formConfig = {
            action: this.getDownload(file),
            method: 'get',
        }
        Object.entries(formConfig).map(
            ([key, val]) => form.setAttribute(key, val)
            )
        form.submit()
    },
    closeFile(file) {
        this.newfileList = []
        for(let i = 0;i<this.fileLists.length;i++){
            if(this.fileLists[i] == file){
                // console.log(this.fileLists[i])
            }else{
                this.newfileList.push(this.fileLists[i])
            }
        }
        // console.log(this.newfileList)
        this.fileLists = this.newfileList

    },
    // 
    fileRemvoeFunc (file, fl) {
      // this.fileList = fl
      fl.length === 0 && (this.tipFlag = true)
    },
    // 
    uploadCtrlFunc (res, file, fl) {
        this.fileList = fl
        // console.log(this.fileList)
        this.loading = false
        this.$emit('confirm')
        // this.done()
        this.$message({
            type: 'success',
            message: ''
        })
        this.$refs.upload.clearFiles()
    },
    // 
    fileErrorFunc (err, file, fileList) {
        this.loading = false
        console.log('error: ', err, file, fileList)
        this.$message({
            type: 'error',
            message: ''
        })
    },
    // loading
    beforeUploadFunc (file) {
        if (file) {
            this.loading = true
            let fileLimitSize = 30
            if (file.size) {
                const limitSize = file.size / 1024 / 1024 < fileLimitSize
                if (!limitSize)  {
                  this.$message.warning(` ${fileLimitSize} MB!`)
                }
                return  limitSize && fileLimitSize
            }
        }
    },

    // 
    handleExceed (file, fileList) {
        this.$message({
            message: '',
            type: 'warning'
        })
    },
    // 
    handleUploadChange (file, fl) {
    //   console.log(file)
    //   console.log(fl)
        fl.length > 0 && (this.tipFlag = false)
      // this.fileList = JSON.parse(JSON.stringify(fl))
    },
    // 
    uploadSectionFile(file){
        this.postparams.append('file', file.file);
    },
    updateCompany(formDatas){
        this.isDisable = true
        let postparam = {
            ecooperation:{
                partnerName:this.FormModel.epartnerpartnerName,
                partnerCallintime:this.FormModel.ecooperationpartnerCallintime,
                partnerBdOwner:this.FormModel.ecooperationpartnerBdOwner,
                partnerCostage:this.FormModel.ecooperationpartnerCostage,
                signContract:this.FormModel.ecooperationsignContract,
                contractDate:this.FormModel.ecooperationcontractDate,
                entrust:this.FormModel.ecooperationentrust,
                entrustName:this.FormModel.ecooperationentrustName,
                partnerAwarding:this.FormModel.ecooperationpartnerAwarding,
                projectName:this.FormModel.ecooperationprojectName,
                coType:this.FormModel.ecooperationcoType,
                coProgress:this.FormModel.ecooperationcoProgress,
                fiiCodepartment:this.FormModel.ecooperationfiiCodepartment,
                },
            epartner : {
                partnerName:this.FormModel.epartnerpartnerName,
                partnerIndustry:this.FormModel.epartnerpartnerIndustry,
                partnerRegion:this.FormModel.epartnerpartnerRegion,
                partnerProduct:this.FormModel.epartnerpartnerProduct,
                partnerList:this.FormModel.epartnerpartnerList,
                partnerScale:this.FormModel.epartnerpartnerScale,
                partnerAddr:this.FormModel.epartnerpartnerAddr,
                partnerWebddr:this.FormModel.epartnerpartnerWebddr,
                partnerTech:this.FormModel.epartnerpartnerTech,
                partnerDate:this.FormModel.epartnerpartnerDate,
                partnerChannel:this.FormModel.epartnerpartnerChannel,
                partnerTurnover:this.FormModel.epartnerpartnerTurnover,
                partnerRegistcapital:this.FormModel.epartnerpartnerRegistcapital,
                partnerNo:this.partnerNo,
            },
            eccontacts: {
                name:this.FormModel.eccontactsname,
                gender:this.FormModel.eccontactsgender,
                phoneNumber:this.FormModel.eccontactsphoneNumber,
                partnerName:this.FormModel.epartnerpartnerName,
                emailAddress:this.FormModel.eccontactsemailAddress,
                title:this.FormModel.eccontactstitle,
            },
            userInfo:{
                userCode:localStorage.getItem('USERCODE'),
                userName:localStorage.getItem('USERNAME'),
            },
            fileName :this.newfileList
        }
        // 
        this.postparams = new FormData()
        // this.$refs.upload.submit()
        this.postparams.append('ecooperation',JSON.stringify(postparam.ecooperation));
        this.postparams.append('epartner', JSON.stringify(postparam.epartner));
        this.postparams.append('eccontacts', JSON.stringify(postparam.eccontacts));
        this.postparams.append('userInfo', JSON.stringify(postparam.userInfo));

        let self = this;
        this.$refs[formDatas].validate(valid => {
            if (valid) {
            this.$refs.upload.submit()

            this.$store.dispatch('updateCompany',this.postparams).then((rep) => {
                if(rep.code == 0){
                    alert('')
                    setTimeout(() => {
                        this.isDisable = false
                    }, 1000)
                    //
                    if(this.fileLists.length != this.fileAll.length){
                        this.$store.dispatch('updateCompany2',postparam).then((rep) => {
                            // if(rep.code == 0){
                            //         alert('')    
                            // }else {
                            //     return false;
                            // }
                        });
                    }
                }else {
                    alert(rep.message)
                    this.isDisable = false
                    return false;
                }
            });

        } else {
            // console.log("error submit!!");
            alert("")
            this.isDisable = false
            return false;
        }
      })
    },
    // <!---->
    gotoLogin() {
        this.$router.go(-1);
    },
    // 
    checkMobile(str) {
        let re = /^1\d{10}$/
        if (re.test(str)) {
            return true;
        } else {
            return false;
        }
    },

    }
}
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.register-l{
    float: left;
    width: 80%;
    margin-left: 10%;
}
.el-input{
    width: 100%;
}
.el-select{
    width: 100%;
}
.titleSpan{
    margin:30px 0;
    font-size: 1.5em;
    color:#030303;
}
</style>


