/**
 * 
 */
import { api } from './api';
import axios from 'axios';
import { configUrl } from '../config';

const actions = {

    //
    searchUser ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.searchUser,
            method: 'post',
            baseURL: configUrl.baseUrl,// baseUrl  chenUrl
            data: param
        }).then((rep) => {
          return (function() {  
            return rep.data;
          })();
        });
        
      },
    //
    searchUserMsg ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.searchUserMsg,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
    },
    //
    savechange ({commit, dispathc, state, rootState}, param) {
    //   console.log(param);
        return axios({
            url: api.savechange,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
        //   console.log(rep)
            return rep;
        });
        
    },
     
    //
    premodifyPwd ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.premodifyPwd,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //
    submitModify ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.submitModify,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //
    getCompanyData ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.getCompanyData,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //
    modifyUserMsg ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.modifyUserMsg,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //
    hideCompany ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.hideCompany,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //
    delCompany ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.delCompany,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //
    addCompany ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.addCompany,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param,
            // headers: {
            //     'Content-Type': 'multipart/form-data'
            // },
        }).then((rep) => {
            return rep;
        });
          
    },
    //
    getEcooperationList ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.getEcooperationList,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //
    updateCompany ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.updateCompany,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //
    updateCompany2 ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.updateCompany2,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //http://127.0.0.1:8080/import
    upload ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.upload,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param,
           
        }).then((rep) => {
            return rep;
        });
          
    },
    
    //export
    export ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.export,
            method: 'get',
            baseURL: configUrl.baseUrl,
            data: param,
            headers: {
                'Content-Type': 'multipart/form-data'
            },
            responseType: 'blob'
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //
    filedetail ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.filedetail,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param,
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //
    testdownload({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.testdownload,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param,
        }).then((rep) => {
            return rep.data;
        });
          
    },
}

export default actions;



